/* TYPOGRAPHY */

.link-to-sign-in,
.link-to-sign-in:visited {
  text-decoration: none;
  color: var(--blue);
  margin-top: 4em;
  text-align: center;
}

.error-message {
  color: var(--red);
  text-align: center;
}

.auth-page-label {
  font-family: "Acme", sans-serif;
  color: var(--blue);
  text-shadow: 3px 3px var(--gray);
  text-align: center;
  font-size: 2.5rem;
}

/* BUTTONS */
.button {
  outline: none;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.button-auth {
  margin: 0.5em;
  font-size: 1.6rem;
  background: var(--blue);
  color: var(--gray);
  font-family: "Acme", sans-serif;
  padding: 0.5em 1em;
  cursor: pointer;
  outline: none;
  box-shadow: 3px 3px var(--gray);
}

.button-auth:hover {
  background: var(--red);
}

.button-auth-container,
.button-sign-in-container {
  margin: 2em auto;
  display: flex;
  justify-content: center;
}

.button-google-sign-in,
.button-email-sign-in,
.button-email-sign-up {
  background-color: var(--red);
  color: var(--gray);
  font-size: 1rem;
  padding: 0.5em 1em;
  margin: 0 0.25em;
}

/* FORM */

.input {
  border-radius: 5px;
  border: none;

  outline: none;
  padding: 0.5em;
}

.sign-in-input {
  margin: 0.25rem 0;
  width: 100%;
  font-size: 1.5rem;
  background: var(--blue);
  color: var(--gray);
}

label {
  margin: 0.25rem 0;
}

.email-authentication,
.sign-in {
  color: white;
  padding: 0.25rem;
  border-radius: 5px;
  width: 80%;
  max-width: 500px;
  min-width: 275px;
  margin: 1.5em auto;
}

/* LAYOUT */

.hide-error {
  display: none;
}

.show-error {
  display: block;
}

.auth-page {
  height: 97vh;
  padding: 5em;
  z-index: 1;
}

/* MEDIA QUERIES */

@media (max-width: 550px) {
  .button-auth {
    font-size: 1.2rem;
  }

  .auth-page {
    padding: 5em 1em;
  }

  .sign-in-input {
    font-size: 1.25rem;
  }

  .auth-page-label {
    font-size: 2.5rem;
  }
}

@media (max-width: 450px) {
  .button-sign-in-container {
    flex-direction: column;
    align-items: center;
  }

  .button-email-sign-in {
    margin-bottom: 0.5em;
  }

  .sign-in-input {
    font-size: 1rem;
  }

  .button-google-sign-in,
  .button-email-sign-in,
  .button-email-sign-up {
    font-size: 0.9rem;
    min-width: 170px;
  }
}
